body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Navigation {
    text-align: right
}
.logo{
    padding-top: 5px;
}
ul {
    list-style: none;
}
ul li {
    display: inline;
    margin: auto;
}

.mainClass {
  padding-bottom: 20px;
}
.logoImage{
    float: left;
    padding-top: 5vh;
    padding-left: 2vh;
    height:30vh;
    min-height: 40px;
    min-width: 40px;
}
.LogoClass {
    margin: auto;
    text-align: center;
    width: 100vw;
    float: none;
}
.logoClass {
    text-align: center;
    width: 100vw;
    padding-top: 10px;
    padding-bottom: 10px;
}
.container{
    margin: auto;
    text-align: center;
    width: 60vw;
    padding-top:200;
    display: block;
}

.List {
    width: 20vw;
}
.leftContainer {
    float: left;
}

.rightContainer {
    padding-left: 5vh;
    float: left;
    text-align: center;
}
a {
    text-decoration: none;
}

i:hover{
    color: DarkTurquoise ;
    font-weight: bold;
}

.footer {
    float: none;
    clear: both;
    font-weight: bolder;
    position: relative;
    left: 0;
    bottom: 0;
    width: 100vw;
    text-align: center;
    padding-top: 10px;
}
.App {
  text-align: center;
}

